<template>
  <div class="big-table-wrap" ref="tableWrap">
    <div class="search-wrap" ref="searchWrap">
      <Row :gutter="16">
        <Col span="6">
          <div  class="input-item">
            <span class="input-label">应用名称:</span>
            <Input v-model="userAccount" placeholder="应用名称" class="input-width"></Input>
          </div>
        </Col>
        <Col span="2">
          <div  class="input-item right" >
            <Button type="primary" @click="reSearchInfo" >查询</Button>
          </div>
        </Col>
      </Row>
    </div>
    <!--操作按钮区域-->
    <div class="handle-wrap" ref="handleWrap" >
      <div class="handle-btn-wrap">
        <Button type="primary" @click="addInfoWrapShow">初始化私钥</Button>
      </div>

    </div>
    <!--table表格区域-->
    <div class="table-wrap-style" ref="tableBox">
      <Table  :loading="loading" :height="tableHeight" :data="tableData" :columns="tableColumns"  stripe border></Table>
    </div>
    <div class="page-wrap" ref="pageWrap">
      <div class="right">
        <Page :total="total" :current="page" @on-change="changePage" @on-page-size-change="changeSize" show-total show-sizer :page-size-opts="pageSizeOpts"></Page>
      </div>
    </div>
    <!--详情弹窗-->
    <Modal
      v-model="detailWrap"
      title="详情">
      <div>
        <div class="detail-one-line">
          <div class="detail-label">应用名称：</div>
          <div class="detail-value">{{currentChoiceDetail.child_desc_app}}</div>
        </div>
        <div class="detail-one-line">
          <div class="detail-label">应用属性授权码：</div>
          <div class="detail-value">{{currentChoiceDetail.AuthorizedCode}}</div>
        </div>
        <div class="detail-one-line">
          <div class="detail-label">公钥：</div>
          <div class="detail-value">{{currentChoiceDetail.Secret}}</div>
        </div>
        <div class="detail-one-line">
          <div class="detail-label">当前密钥：</div>
          <div class="detail-value">{{currentChoiceDetail.SecretKey}}</div>
        </div>
        <div class="detail-one-line">
          <div class="detail-label">首次发行时间：</div>
          <div class="detail-value">{{currentChoiceDetail.create_time}}</div>
        </div>
        <div class="detail-one-line">
          <div class="detail-label">更新时间：</div>
          <div class="detail-value">{{currentChoiceDetail.update_time}}</div>
        </div>
        <div class="detail-one-line">
          <Button type="primary" long @click="resetKey">重新生成私钥</Button>
        </div>
      </div>
      <div slot="footer">
        <Button type="primary"   @click="detailWrap=false">关闭</Button>
      </div>
    </Modal>
    <!--新增弹窗   -->
    <Modal
      v-model="addWrap"
      title="新增">
      <div>
        <div  class="input-item">
          <span class="input-label">应用名称：</span>
          <Select v-model="currntMerchant" class="input-width" multiple clearable filterable label-in-value  @on-change="merchantChange">
            <Option v-for="item in merchantList" :value="item.child_id_app" :key="item.child_id_app" class="input-width">{{ item.child_desc_app }}</Option>
          </Select>
        </div>
        <div  class="input-item">
          <span class="input-label">描述：</span>
          <Input v-model="currentType" placeholder="描述" class="input-width"></Input>
        </div>
      </div>
      <div slot="footer">
        <Button   @click="addWrap=false">取消</Button>
        <Button type="primary"  :loading="loadingAdd" @click="addInfo">确定</Button>
      </div>
    </Modal>
  </div>
</template>
<script>
  import { tableMethods } from "../../../js/mixintable";
  export default {
    data () {
      return {
        curUserName : sessionStorage.getItem('user').replace(/\"/g, ""),
        /*搜索区域 条件*/
        userAccount:'',// 账号
        loading:false,
        detailWrap:false,//详情弹窗
        currentChoiceDetail:{},
        addWrap:false,//新增信息弹窗
        addInfoDetail:{},//新增信息的详情
        currntMerchant:'',//新增-  初始化私钥
        currentCheckAll:[], //新增，   所有选中的信息
        currentType:'',//新增--- 描述
        loadingAdd:false,//遮罩 Model
        merchantList:[
          {
            child_name_app: 'yindian',
            child_id_app: '123',
            child_desc_app: '银点描述',
          },
          {
            child_name_app: 'kuaile',
            child_id_app: '222',
            child_desc_app: '快乐农家描述',
          }
        ],
        tableData:[],
        /*table表格*/
        tableColumns: [
          {
            title: '应用名称',
            align: 'center',
            width:100,
          /*  sortable: 'custom',*/
            key:'child_desc_app'
          },
          {
            title: '应用ID',
            key: 'child_id_app',
            align: 'center',
           /* sortable: 'custom',*/
            width:80
          },

          {
            title: '授权码',
            key: 'authorizedcode',
            align: 'center',
            tooltip:true
          },
          {
            title: '私钥',
            key: 'secretkey',
            align: 'center',
            tooltip:true
          },
          {
            title: '首次发行时间',
            align: 'center',
            key: 'create_time',
            tooltip:true
          },
          {
            title: '更新时间',
            align: 'center',
            key: 'update_time',
            tooltip:true
          },
          {
            title: '操作员',
            key: 'operate_user',
            align: 'center',
            tooltip:true
          },
          {
            title: '操作',
            key: 'action',
            width: 250,
            align: 'center',
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'primary',
                    size: 'small'
                  },
                  style: {
                    marginRight: '5px'
                  },
                  on: {
                    click: () => {
                      console.log(params.index)
                      this.resetKey(params);
                    }
                  }
                }, '重置私钥'),
                h('i-switch', {
                  props: {
                    type: 'error',
                    size: 'large',
                    value:params.row.using_flag==1?true:false
                  },
                  style: {
                    marginRight: '5px'
                  },
                  on: {
                    "on-change": () => {
                      console.log(params.index)
                      this.deleteInfo(params)
                    }
                  }
                }, [
                  h('span', {
                      slot: 'open',
                  }, '开启'),
                  h('span', {
                      slot: 'close',
                  }, '禁用'),
                ])
              ]);
            }
          }

        ]
      }
    },
    components: {
    },
    mixins: [tableMethods],
    mounted() {
      this.$nextTick(function () {
        //获取用户状态
      //  this.getUserState()
        //查询数据
         this.searchInfo();
      })
    },
    methods: {
      //查询数据
      searchInfo(){
        let that=this
        this.loading=true;
        let objData={
          type:"QueryAllServiceAttrBL",
          call_time:myUtil.getTime(),
          data:{
              child_name_app:this.userAccount,
              page:this.page,
              size:this.size
          }
        }
        let jsonData = protocol.getJsonParams(protocol.keyAllInfo, objData);
        http.ajax('middle', jsonData, true, (data) => {
          this.loading=false;
          this.tableData = data.data;
          this.total =data.total-0;
        },()=>{
          this.loading=false;
        })
      },
      //获取应用名称下拉列表
      getUserState(){
        let jsonData = protocol.getJsonParams(protocol.keyAllInfo, {});
        http.ajax('', jsonData, true, (data) => {
          this.loading=false;
          console.log(data)
          this.statueList = this.statueList.concat( data.returnList);
          this.total =data.total-0;
        })
      },
      //显示当前选中的积分商详情
      showDetail(params){
        this.detailWrap=true
        let jsonData = protocol.getJsonParams(protocol.keyAllInfo, {
          type:"QuerySingleServiceAttrBL",
          call_time:myUtil.getTime(),
          data:{
            child_id_app:params.row.child_id_app
          }
        });
        http.ajax('middle', jsonData, true, (data) => {
          this.currentChoiceDetail=Object.assign({},data.data[0]);
        })
      },
      /*新增-------------------------------------*/
      //添加信息，显示弹窗
      addInfoWrapShow(){
          this.addWrap=true;
          /*初始化数据*/
          this.currntMerchant=''
          this.currentType=''
      },
      //添加信息显示弹窗
      addInfo(){
        //判断下拉是否为空
        if(this.currntMerchant==''){
          this.$Message.info('请选择应用名称！');
          return;
        }
        this.currentCheckAll
        for(let i=0;i<this.currentCheckAll.length;i++){
          this.currentCheckAll[i].operate_user=this.curUserName
        }
        let data={
          type:"AddServiceAttrBL",
          call_time:myUtil.getTime(),
          data:this.currentCheckAll
        }
        let jsonData = protocol.getJsonParams(protocol.keyAllInfo, data);
        http.ajax('middle', jsonData, true, (data) => {
          this.addWrap=false;
          this.searchInfo();

        })


/*        this.$Modal.confirm({
          title: '提示',
          content: "这是生产环境，确定要为 "+this.currntMerchantName + "&nbsp;&nbsp;"+  this.currentTypeName+" 产生密钥吗？",
          onOk: ()=>{
           // this.addWrap=false;
           //发送请求，保存信息
          },
          onCancel: ()=>{

          }
        })*/
      },

      //取消新增
      cancelAdd(){

      },
      //积分商下拉选中修改
      merchantChange(data){
        let that=this
        if(data){
          this.currentType=''
          that.currentCheckAll=[];
          for(let i=0;i<data.length;i++){
            this.merchantList.forEach(function(item){
              if(data[i].value==item.child_id_app){
                that.currentType+=item.child_name_app+','
                that.currentCheckAll.push(item)
                return
              }
            })
          }
        }
      },
      /*删除--------------------------------------*/
      //删除信息提示
      deleteInfo(params){
        console.log('删除了')
        let jsonData = protocol.getJsonParams(protocol.keyAllInfo, {
          type:"DeleteServiceAttrBL",
          call_time:myUtil.getTime(),
          data:{
            child_id_app:params.row.child_id_app,
            using_flag:params.row.using_flag==1?'-1':'1'
          }
        });
        http.ajax('middle', jsonData, true, (data) => {
          var msg=params.row.using_flag==1?'禁用成功':'开启成功'
          this.$Message.info(msg);
          //删除信息后重新获取列表
            this.searchInfo();
        })
      },
      //重新生成密钥
      resetKey(params){
        let jsonData = protocol.getJsonParams(protocol.keyAllInfo, {
          type:"UpdateServiceAttrBL",
          call_time:myUtil.getTime(),
          data:{
            child_id_app:params.row.child_id_app
          }
        });
        http.ajax('middle', jsonData, true, (data) => {
          this.$Message.info('更新完成！');
          this.searchInfo();
         /* this.$set(this.currentChoiceDetail,'SecretKey',data.resultData.secret_key)*/
        })
      },
      /*历史窗体------------------------*/
      //显示历史弹窗
/*      historyInfoShow(){
        this.detailHistoryWrap=true;
        //获取历史信息

      },*/
      //点击查看私钥
/*      getKey(){
         console.log('查看私钥')
      },*/
      detailHistoryWrapHide(){
        //this.detailHistoryWrap=false
      },
    },
  }
</script>
<style lang="less" scoped>
  @import '../../../css/common.css';
  .input-label{
    width:60px;
  }
  .detail-one-line{
    display: flex;
    padding:5px;
  }
  .detail-label{
    width:85px;
    line-height: 30px;
  }
  .detail-value{
    padding:5px 10px;
    box-sizing: border-box;
  }
</style>



